.container {
  position: fixed;
  top: 1.5rem;
  right: 1.5rem;

  display: flex;
  justify-content: flex-end;
  z-index: 999;
}

.popup {
  position: absolute;
  top: 100%;
  right: 0;

  max-width: 100vw;

  margin-top: .5rem;
  z-index: 999;
  padding: 1rem;
  background: white;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 3px;

  color: #222;
}

.add > * {
  margin-right: 0 !important;
  margin-top: -5px !important; 
}

.date > * {
  width: 100%;
}

.amount {
  font-weight: 600;
}


.positive {
  color: #8BC34A;  
}

.negative {
  color: #d01919;  
}
